<template>
  <div class="user_container">
    <!-- 卡片面板 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!-- 搜索框 -->
        <el-input
          placeholder="请输入内容"
          v-model="params.query"
          class="input-with-select"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getusersdata"
          ></el-button>
        </el-input>
        <el-button type="primary" @click="addUser"
          >{{ params.query }}添加用户</el-button
        >
        <!-- 搜索框 -->
      </div>

      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column
          label="#"
          width="60"
          type="index"
          sortable
        ></el-table-column>
        <el-table-column label="姓名" width="180" prop="username">
        </el-table-column>
        <el-table-column label="邮箱" width="180" prop="email" sortable>
        </el-table-column>

        <el-table-column label="电话" width="180" prop="mobile">
        </el-table-column>
        <el-table-column label="角色" width="180" prop="role_name">
        </el-table-column>

        <el-table-column label="状态" width="180">
          <template slot-scope="scope">
            <!-- {{scope.row}} -->
            <el-switch
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </template>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="edit_user(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="del_user(scope.row.id)"
            ></el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="setroles(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!-- 分页 -->
    </el-card>
    <!-- 卡片面板 -->

    <!-- 添加用户对话框 -->
    <el-button type="text" @click="dialogFormVisibleAdd = true"></el-button>
    <el-dialog title="添加用户" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="formAdd" :rules="rules" ref="formAdd">
        <el-form-item
          label="用户名"
          :label-width="formLabelWidth"
          prop="username"
        >
          <el-input v-model="formAdd.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item
          label="密码"
          prop="password"
          :label-width="formLabelWidth"
        >
          <el-input v-model="formAdd.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="formAdd.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="电话" :label-width="formLabelWidth" prop="mobile">
          <el-input v-model="formAdd.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
        <el-button type="primary" @click="sure">确定</el-button>
      </div>
    </el-dialog>

    <!-- 编辑对话框 -->
    <el-button type="text" @click="dialogFormVisibleEidt = true"></el-button>
    <el-dialog title="编辑用户" :visible.sync="dialogFormVisibleEidt">
      <el-form :model="formEidt" :rules="rules" ref="formEidt">
        <el-form-item
          label="用户名"
          :label-width="formLabelWidth"
          prop="username"
        >
          <el-input
            v-model="formEidt.username"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>

        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="formEidt.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="电话" :label-width="formLabelWidth" prop="mobile">
          <el-input v-model="formEidt.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEidt = false">取 消</el-button>
        <el-button type="primary" @click="sure_edit">确定</el-button>
      </div>
    </el-dialog>

    <!-- 分配角色 -->
    <el-dialog
      title="分配角色"
      :visible.sync="dialogFormVisibleSet"
      class="setroles"
    >
      <el-form :model="roleform">
        <el-form-item
          label="当前的用户:"
          :label-width="formLabelWidth"
          prop="username"
        >
          <span>{{ roleform.username }}</span>
        </el-form-item>
        <el-form-item
          label="当前的角色:"
          :label-width="formLabelWidth"
          prop="role_ame"
        >
          <span>{{ roleform.roleName }}</span>
        </el-form-item>

        <el-form-item label="分配新角色:" :label-width="formLabelWidth">
          <el-select v-model="value" clearable placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.roleName"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleSet = false">取 消</el-button>
        <el-button type="primary" @click="sure_setroles">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    // 自定义校验规则
    var validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error('密码至少6位数'))
      } else {
        callback()
      }
    }
    // 校验邮箱
    var validateEmail = (rule, value, callback) => {
      var reg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/
      if (!reg.test(value)) {
        callback(new Error('邮箱格式不正确'))
      } else {
        callback()
      }
    }
    return {
      total: 0,
      params: {
        query: '',
        pagenum: 1,
        pagesize: 4
      },
      tableData: [],
      currentPage4: 4,
      // 添加用户
      dialogTableVisible: false,
      dialogFormVisibleAdd: false,
      dialogFormVisibleEidt: false,
      dialogFormVisibleSet: false,
      formAdd: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },

      formLabelWidth: '100px',
      // 规则
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          {
            min: 3,
            max: 11,
            message: '长度在 3 到 11 个字符',
            trigger: 'blur'
          }
        ]
      },
      formEidt: {},
      // 编辑分配用户权限
      options: [
        {
          value: '选项1',
          label: '黄金糕'
        }
      ],
      roleform: { name: '', username: '', roleName: '', id: 0 },
      value: ''
    }
  },
  created () {
    this.getusersdata()
    this.getrolesdata()
  },
  methods: {
    async getusersdata () {
      const res = await this.$http.get('users', { params: this.params })
      this.tableData = res.data.data.users
      this.total = res.data.data.total
    },

    // 分页
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`, 777777)
      this.params.pagesize = val
      this.getusersdata()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`, 666666)
      this.params.pagenum = val
      this.getusersdata()
    },

    // 添加用户
    addUser () {
      // 1.点击按钮显示弹框
      // 2.布局弹框样式
      // 3.输入进行验证
      // 4.点击确认进行二次验证
      // 5.发送请求
      // 6.成功后弹出成功消息
      // 7.关闭弹框并清除表单内容
      this.dialogFormVisibleAdd = true
    },

    // 点击确认二次校验
    sure () {
      this.$refs.formAdd.validate(async vald => {
        if (vald) {
          const res = await this.$http.post('users', this.formAdd)
          if (res.data.meta.status === 201) {
            this.getusersdata()
            this.$message({
              message: '恭喜添加成功',
              type: 'success'
            })
            this.formAdd = {}
            this.dialogFormVisibleAdd = false
          }
        } else {
          return false
        }
      })
    },

    // 删除用户
    del_user (id) {
      this.$confirm('此操作将删除该信息', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          await this.$http.delete('users/' + id)
          this.getusersdata()
        })
        .catch(err => {
          return err
        })
    },

    // 编辑用户信息
    edit_user (item) {
      this.dialogFormVisibleEidt = true
      this.formEidt = item
    },

    sure_edit () {
      this.$refs.formEidt.validate(async vald => {
        if (vald) {
          const res = await this.$http.put('users/' + this.formEidt.id, {
            email: this.formEidt.email,
            mobile: this.formEidt.mobile
          })
          if (res.data.meta.status === 200) {
            this.getusersdata()
            this.$message({
              message: '编辑成功',
              type: 'success'
            })
            this.dialogFormVisibleEidt = false
          }
        } else {
          return false
        }
      })
    },

    // 获取角色列表
    async getrolesdata () {
      const res = await this.$http.get('roles')
      console.log(res.data.data, 111111)
      this.options = res.data.data
    },

    // 分配角色
    setroles (row) {
      this.dialogFormVisibleSet = true
      this.roleform.username = row.username
      this.roleform.roleName = row.role_name
      this.roleform.id = row.id
    },
    // 点击确认添加角色
    async sure_setroles () {
      await this.$http.put('roles/' + this.roleform.id, {
        roleName: this.roleform.roleName
      })
      this.getrolesdata()
      this.$message({
        message: '编辑成功',
        type: 'success'
      })
      this.dialogFormVisibleSet = false
    }
  }
}
</script>
<style lang="less" scoped>
.user_container {
  height: 100%;
  .el-input {
    width: 40%;
  }
  .el-form {
    .el-input {
      width: 100%;
    }
  }

  .add {
    margin-left: 20px;
  }
  .box-card {
    margin-top: 20px;
  }
  .setroles {
    .el-form-item {
      margin-bottom: 0;
    }
  }
}
</style>
